/* Base Styles */
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--main-heading-color);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.16;
  font-family: -apple-system, BlinkMacSystemFont, "Poppins", "Segoe UI",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}

img {
  display: block;
  height: fit-content;
  width: 100%;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

a:visited {
  color: none;
}

button,
input {
  background-color: inherit;
}

button {
  cursor: pointer;
}

/* Variables */
:root {
  --label-radio-bg-color: #8fa1d0;
  --label-radio-color: #8fa1d0;

  --label-radio-pink-color: #e09cb5;
  --label-radio-green-color: #bedbb0;

  --title-welcome-color: #161616;
  --bg-welcome-color: #161616;
  --text-hover-welcome-color: #ffffff;
  --icon-user-fill-color: #1f1f1f;
  --icon-user-stroke-color: #ffffff;
  --icon-user-plus-fill-color: #bedbb0;
  --icon-user-plus-stroke-color: #121212;
  --welcome-bg-gradient: linear-gradient(
    180deg,
    #ffffff 0%,
    #ffffff 25%,
    #bedbb0 92.19%
  );
  --primary-color: #2e225f;
  --secondary-color: #2e1746;
  --accent-color: #ffc727;
  --gradient-background: linear-gradient(
    270deg,
    var(--secondary-color) 3.2%,
    var(--primary-color) 99.98%
  );
  --text-color: #fff9;
  --text-color-light: #fbfbfb;
  --button-background: linear-gradient(
    97deg,
    var(--accent-color) -16.42%,
    #9e40ba 97.04%,
    #7000ff 150.71%
  );
  --button-hover-background: linear-gradient(90deg, #e86805, #9e001c 70%);
  --form-background: #ffffff1a;
  --box-shadow: 0px 4px 60px 0px #00000040;
  --blur: blur(50px);
  --font-size-large: 26.963px;
  --font-size-medium: 18px;
  --font-size-small: 16px;
  --font-weight-normal: 400;
  --border-radius: 8px;
  --transparency-60: #fff9;
  --icon-violet: #734aef;
  --fundal: #1b1e23;
  --panou: #23252c;
  --accent: #37e673;
  --rosu: #ea3c53;
  --text: #e0e6ed;
  --gri: #3a3d46;
  --gri-deschis: #c7cad4;
  --tab-activ: #30313b;
  --radius: 18px;
  --radius-mic: 9px;
  --avatar-bg: #22272e;
  --white: #fbfbfb;
  --brand-color: #fc842d;
  --Dark: #1c2434;
  --White: #fff;
  --Text-color: #64748b;
  --Primary: #3c50e0;
  --Gray: #eff4fb;
  --Stroke: #e2e8f0;
  --TailGrids-Text-Color: #637381;
  --LastMessage: #5d657b;
  --Green: #10b981;
  --Red: #dc2626;
  --Gray-2: #f8fafc;
}

